        default: 'sans-serif'
      }, {
        name: '$monospace-font',
        type: 'string',
        default: 'monospace'
      }]
    };
  },
  watch: {
    textTransformClass: function textTransformClass(val) {
      this.textTransformPreview = "<div".concat(val ? ' class="' + val + '"' : '', ">...</div>");
    },
    alignClass: function alignClass(val) {
      this.alignPreview = "<p".concat(val ? ' class="' + val + '"' : '', ">...</p>");
    },
    fontWeightClass: function fontWeightClass(val) {
      this.fontWeightPreview = "<p".concat(val ? ' class="' + val + '"' : '', ">...</p>");
    },
    fontFamilyClass: function fontFamilyClass(val) {
      this.fontFamilyPreview = "<div".concat(val ? ' class="' + val + '"' : '', ">...</div>");
    },
    wordWrapClass: function wordWrapClass(val) {
      this.wordWrapPreview = "<div".concat(val ? ' class="' + val + '"' : '', ">...</div>");
    }
  }
};